/**
 * GIBI Player
 */
'use strict';

import Hls from 'hls.js';
import {EventEmitter} from '../libs/utils/EventEmitter';
import {Logger} from '../libs/utils/logger';
import BrowserSpec from './browser-spec';
import MovizoReporter from './movizo-reporter';
// import {CustomPlaylistLoader} from '../libs/loader/CustomPlaylistLoader';
import {CustomFetchLoader} from '../libs/loader/CustomFetchLoader';

// InlinePlayerControls controls='movizo' 
import InlinePlayerControls from '../../inline-player.js/src/player-controls.js';

// 
// 
import InlineVideoDisplay from './inline-video-display';

const LOGGER = new Logger(false);

class MovizoPlayer {

    static _mergeOptions(options, defaults) {
        if (defaults != undefined) {
            for (var key in defaults) {
                if (!(key in options)) {
                    options[key] = defaults[key];
                }
            }
        }
        return options;
    }

    static _resolveOptions(options, defaults) {
        if (defaults === undefined) {
            defaults = MovizoPlayer.options;
        }
        if (options === undefined) {
            options = {};
        }

        // inline
        if (options.videoDisplay == "inline") {
            InlineVideoDisplay.defaultOptions(options);
        }

        var merges = MovizoPlayer._mergeOptions(options, defaults);

        // inline
        if (merges.videoDisplay == "inline") {
            InlineVideoDisplay.finalOptions(merges);
        }

        return merges;
    }

    static _resolveURL(options, fileName, urlPrefix) {
        if (urlPrefix === undefined)
            urlPrefix = options.movieURL;

        var path = this._resolvePath(options, fileName);
        var url = `${urlPrefix}/${path}`;
        if (options.refreshQuery) {
            // IEstartsWith
            //if (!options.refreshQuery.startsWith('?')) {
            if (options.refreshQuery[0] !== '?') {
                url += '?';
            }
            url += options.refreshQuery;
        }
        return url;
    }

    static _resolvePath(options, fileName) {
        return `${options.accountID}/${options.movieID}/${fileName}`;
    }

    static posterURL(options) {
        if (options.staticPosterURL != null) {
            return options.staticPosterURL;
        }
        options = MovizoPlayer._resolveOptions(options);
        return this._resolveURL(options, 'poster', options.posterURL);
    }

    static thumbnailURL(options) {
        options = MovizoPlayer._resolveOptions(options);
        return this._resolveURL(options, 'thumbnail', options.posterURL);
    }

    //  value 
    //  movizoPlayer.swf  value  '' 
    //static _movizoPlayerSwfCallback(eventName, instanceID, value) {
    static _movizoPlayerSwfCallback(eventName, instanceID) {
        var player = this.getInstance(instanceID);
        if (player != null) {
            player.triggerEvent(eventName);
        }
    }

    static getInstance(instanceID) {
        if (this.instances != null) {
            return this.instances[instanceID];
        }
        return null;
    }

    static bindInstance(instance, instanceID) {
        if (this.instances == null) {
            this.instances = {};
        }
        this.instances[instanceID] = instance;
    }

    static unbindInstance(instance, instanceID) {
        if (this.instances != null) {
            this.instances[instanceID] = null;
        }
    }

    static genInstanceID() {
        var length = 8;
        // 
        var chars = 'abcdefghijklmnopqrstuvwxyz0123456789';
        var charsLen = chars.length;
        var text = '';
        for(var i = 0; i < length; i++){
            text += chars[Math.floor(Math.random()*charsLen)];
        }
        return text;
    }

    constructor(options) {
        options = MovizoPlayer._resolveOptions(options);

        var android = BrowserSpec.androidVersion;
        // Android 2.x (INFOBAR A01)  enableLogs  debug.log.bind 
        if (android && android < '3.0') {
            options.debug = false;
        }

        LOGGER.enabled = options.debug;

        this.options = options;
LOGGER.log(`options...\n${JSON.stringify(options, null, 4)}`)
        this.videoElement = null; // video 
        this.objectElement = null;  // object 
        this.actualElement = null;  //  video or object 
        this.instanceID = MovizoPlayer.genInstanceID();

        MovizoPlayer.bindInstance(this, this.instanceID);

        // observer setup
        var observer = this.observer = new EventEmitter();
        this.on = observer.on.bind(observer);
        this.off = observer.off.bind(observer);
        this.trigger = observer.emit.bind(observer);

        this.pausedByUser = false;    //  ()
        this.pausedByPrivate = false; // 
    }

    destroy() {
        LOGGER.log('destroy');
        MovizoPlayer.unbindInstance(this, this.instanceID);
    }

    load(options) {
        this.options = MovizoPlayer._resolveOptions(options, this.options);

        if (!this.options.accountID) {
            throw new Error('options.accountID not specified.');
        }
        if (!this.options.movieID) {
            throw new Error('options.movieID not specified.');
        }

        if (this.parentElement) {
            // 
            // 
            this.attach(this.parentElement);
        }
    }

    reload(options) {
        this.load(options);
    }

    attach(parentElement) {
        LOGGER.log('attach');

        this.options.accountID = (this.options.accountID || parentElement.getAttribute('data-account-id'));
        this.options.movieID = (this.options.movieID || parentElement.getAttribute('data-movie-id'));

        // 
        this.release();

        this.parentElement = parentElement;

        if (!(this.options.accountID && this.options.movieID)) {
            return null;
        }

        var formats = this.availableFormats();
        for (var i in formats) {
            var format = formats[i];
            this.actualElement = this.createPlayerElement(format);
            if (this.actualElement) {
                this.format = format;
                break;
            }
        }

        if (this.actualElement) {
            parentElement.appendChild(this.actualElement);

            if (this.options.controls == "movizo-scaled" && this.controls)
                this.controls.scale();

            if (this.options.reportURL != null) {
                this.reporter = new MovizoReporter(this, this.options.accountID, this.options.movieID, this.fileName, this.options);
            }

            this.setupEventListener();

            return { format: this.format, module: this.formatModule };
        }
        else {
            LOGGER.log(`could not create element for ${formats}`);
            return null;
        }
    }

    availableFormats() {
        switch (this.options.deliveryType) {
            case 'streaming':
                return ['hls'];
            case 'progressive':
                return ['m4v'];
            case 'hls':
                return ['hls'];
            case 'm4v':
            case 'mp4':
                return ['m4v'];
        }
    }

    setupEventListener() {
        var types = this.supportedEventTypes;
        for (var i in types) {
            this.addEventListener(types[i], this, false);
        }

        var self = this;
        this.handleEvent = function(event) {
            self.trigger(event.type, event);
        };

        // inline
        // if (this.options.videoDisplay == "inline") {
        //     InlineVideoDisplay.setupEventListener(this);
        // }
        // else {
            this.ready = false;
            //this.addEventListener("loadedmetadata", function() {
            this.addEventListener("loadedmetadata", function() {
                LOGGER.log('event loadeddata');
                self.muted = self.options.muted;
                self.ready = true;
            });
            this.addEventListener("loadeddata", function() {
                LOGGER.log('event loadeddata');

                // 
                //  HBS endAt  startAt 
                if (self.options.startAt) {
                    let oldTime = self.currentTime;
                    self.currentTime = self.options.startAt;
                    LOGGER.log(`currentTime: ${oldTime} to ${self.currentTime} (startAt=${self.options.startAt})`);
                }
            });
        // }

        // 
        if (this.options.endAt) {
            this.addEventListener("timeupdate", function() {
                if (self.options.endAt !== null && self.currentTime >= self.options.endAt) {
                    self.pause();
                    self.options.endAt = null;
                }
            });
        }
    }

    get supportedEventTypes() {
        // Flash
        return [
            'load',
            'play',
            'playing',
            'pause',
            'ended',
            'timeupdate',
            'seeking',
            'seeked'];
    }

    release() {
        LOGGER.log('release');

        if (this.actualElement && this.parentElement) {
            this.parentElement.removeChild(this.actualElement);
            this.parentElement = null;
            this.actualElement = null;
        }
        if (this.reporter) {
            this.reporter.destroy();
            this.reporter = null;
        }
    }

    triggerEvent(eventName) {
        if (this.objectElement == null) {
            return;
        }

        /*
      var event = new Event(eventName);
      */
        var event = document.createEvent('HTMLEvents');
        event.initEvent(eventName, true, true);
        this.objectElement.dispatchEvent(event);
    }

    createPlayerElement(format) {
        var movieURL = this.createMovieURL(format);
        switch (format) {
            case 'hls':
                return this.createHlsElement(movieURL);
            case 'm4v':
                return this.createM4vElement(movieURL);
            default:
                return null;
        }
    }

    createMovieURL(format) {
        switch (format) {
            case 'hls':
                return this.resolveMovieURL('m3u8');
            case 'm4v':
                return this.resolveMovieURL('m4v');
            default:
                return null;
        }
    }

    createHlsElement(movieURL) {
        let contentType = null;
        let useVideoElement = false;
        let safari = BrowserSpec.isSafari

        if (this.options.forceHlsPlayer) {
            if (this.options.hlsPlayer == "video") {
                //  Chrome   ['application/vnd.apple.mpegurl', 'application/x-mpegURL']  video.canPlayType  false 
                //  hls.js 
                useVideoElement = true;
                safari = true;
            }
            else {
                useVideoElement = false;
                safari = false;
            }
        }
        else if (this.options.hlsPlayer == "video") {
            useVideoElement = true;
        }
        else if (this.options.hlsPlayer == "hlsjs") {
            useVideoElement = false;
        }
        else if (safari) {
            useVideoElement = true;
        }
        else {
            useVideoElement = true;
            if (Hls.isSupported()) {
                useVideoElement = false;

                // Android 4.4.4 (Nexus5)  Chrome 0 10
                //  VideoElement#currenTime  10
                // hls.js 
                //
                // VideoCloud  chromium forum 
                // https://support.brightcove.com/ja/video-cloud/%E3%82%B5%E3%83%9D%E3%83%BC%E3%83%88%E3%83%89%E3%82%AD%E3%83%A5%E3%83%A1%E3%83%B3%E3%83%88/%E6%97%A2%E7%9F%A5%E3%81%AE%E5%95%8F%E9%A1%8C
                // https://code.google.com/p/android/issues/detail?id=63346
                if (BrowserSpec.androidVersion) {
                    useVideoElement = false;
                }
                // refs #11990: Win10/Edge HLS
                else if (BrowserSpec.edgeVersion) {
                    useVideoElement = false;
                }
            }
        }

        // refs #23533: BrowserSpec.isSafari 
        if (this.options.hlsPlayer == "native" || (useVideoElement && safari)) {
            var types = ['application/vnd.apple.mpegurl', 'application/x-mpegURL'];
            for (var i in types) {
                var type = types[i];
                if (BrowserSpec.videoSupports(type)) {
                    contentType = type;
                    break;
                }
            }
        }

        if (contentType) {
            LOGGER.log(`HLS by VideoElement: hlsPlayer=${this.options.hlsPlayer}, Hls.isSupported=${Hls.isSupported()}, androidVersion=${BrowserSpec.androidVersion}`);
            this.formatModule = 'HTMLVideoElement';
            return this.createVideoSrcElement(contentType, movieURL);
        }
        else {
            LOGGER.log(`HLS by hls.js: hlsPlayer=${this.options.hlsPlayer}, Hls.isSupported=${Hls.isSupported()}, androidVersion=${BrowserSpec.androidVersion}`);
            return this.createHlsJsElement(movieURL);
        }
    }

    createHlsJsElement(movieURL) {
        if (!Hls.isSupported()) {
            LOGGER.log('HLS is not supported');
            return null;
        }

        const elem = this.createVideoElement();

        CustomFetchLoader.setDebug(this.options.debug);

        this.hls = new Hls({
            debug: this.options.debugHlsJs,
            // pLoader: CustomPlaylistLoader,
            fLoader: CustomFetchLoader,

            // autoStartLoad: this.options.autoplay,
            // autoplay  autoStartLoad=true 
            //  autoplay=false  movizo  false 
            //  true 
            autoStartLoad: true
        });
        this.hls.loadSource(movieURL);
        this.hls.attachMedia(this.videoElement);

        // preload
        // metadata
        // 
        if (this.options.autoplay == false && this.options.preload == false) {
            const self = this;
            const stopLoad = function() {
                self.hls.stopLoad();
                self.hls.off(Hls.Events.FRAG_PARSED, stopLoad);
            }
            const startLoad = () => {
                self.hls.startLoad();
                elem.removeEventListener('play', startLoad);
                elem.removeEventListener('timeupdate', startLoad);
                elem.removeEventListener('seeking', startLoad);
            }
            this.hls.on(Hls.Events.FRAG_PARSED, stopLoad);
            elem.addEventListener('play', startLoad);
            elem.addEventListener('timeupdate', startLoad);
            elem.addEventListener('seeking', startLoad);
        }

        LOGGER.log('video tag for hls.js created');
        this.formatModule = 'hls.js';
        return elem;
    }

    createM4vElement(movieURL) {
        // inline
        if (this.options.videoDisplay == "inline") {
            if (InlineVideoDisplay.isSupported() == false) return null;
        }

        var contentType = 'video/mp4';
        if (!BrowserSpec.videoSupports(contentType)) {
            LOGGER.log(`video element does not support ${contentType}.`);
            return null;
        }
        var video = this.createVideoSrcElement(contentType, movieURL);
        this.formatModule = 'HTMLVideoElement';
        return video;
    }

    createVideoSrcElement(contentType, src) {
        var elem = this.createVideoElement();

        if (this.options.withSourceElement) {
            var source = document.createElement('source');
            source.setAttribute('src', src);
            source.setAttribute('type', contentType);
            this.videoElement.appendChild(source);
        }
        else {
            this.videoElement.setAttribute('src', src);
        }

        var android = BrowserSpec.androidVersion;
        if (android && android < '3.0') {
            this.videoElement.setAttribute('onclick', 'if(this.played.length == 0){this.play(); return false;}');
        }

        // inline
        if (this.options.videoDisplay == "inline") {
            InlineVideoDisplay.setVideoElementAttr(this.videoElement);
        }

        LOGGER.log(`video tag for ${contentType} created`);
        return elem;
    }

    createVideoElement() {
        var elem = document.createElement('video');
        this.videoElement = elem;

        if (this.options.crossOrigin) {
            this.videoElement.setAttribute('crossOrigin', this.options.crossOrigin);
        }

        if (this.options.muted) {
            this.videoElement.setAttribute('muted', '');
        }
        if (this.options.controls == true) {
            this.videoElement.setAttribute('controls', '');
        }
        else if (this.options.controls == "movizo" || this.options.controls == "movizo-scaled") {
            elem = this._applyMovizoControls(this.videoElement);
        }
        if (this.options.autoplay) {
            this.videoElement.setAttribute('autoplay', '');
        }
        if (this.options.loop) {
            this.videoElement.setAttribute('loop', '');
        }
        this.videoElement.setAttribute('id', this.options.elementID);
        this.videoElement.setAttribute('class', this.options.elementClass);
        this.videoElement.setAttribute('poster', this.resolvePosterURL());

        var style = this.createVideoStyle();
        if (style !== null) {
            this.videoElement.setAttribute('style', style);
        }

        return elem;
    }

    createVideoStyle() {
        if (this.options.videoSize) {
            var style = '';
            if (this.options.videoSize.width) {
                style += `width:${this.options.videoSize.width};`;
            }
            if (this.options.videoSize.height) {
                style += `height:${this.options.videoSize.height};`;
            }
            if (style.length > 0) {
                return style;
            }
        }
        return null;
    }

    _applyMovizoControls(playerElement) {
        var self = this;
        var elem = document.createElement('div');
        elem.appendChild(playerElement);
        elem.style.width = "100%";
        elem.style.height = "100%";
        elem.style.position = "relative";
        elem.style.left = "0px";
        elem.style.top = "0px";
        playerElement.style.width = "100%";
        playerElement.style.height = "100%";
        playerElement.style.left = "0px";
        playerElement.style.top = "0px";
        playerElement.addEventListener("click", function(){
            if (self.paused)
                self.play();
            else
                self.pause();
        });
        this.controls = new InlinePlayerControls(this, elem);
        this.controls.activate({
            spinner: true,
            autoShow: true,
            volume: true,
            autoplay: this.options.autoplay,
            loop: this.options.loop
        });
        return elem;
    }

    resolveMovieURL(suffix) {
        var profileID = this.options.profileID || 'movie';
        this.fileName = `${profileID}.${suffix}`;
        if (this.options.proxyURL != null) {
            var url = this.options.proxyURL;
            // 
            if (this.options.deliveryType === 'streaming') {
                if (url.indexOf('?') < 0)
                    url += "?";
                else
                    url += "&";
                var date = new Date();
                url += ('0' + date.getHours()).slice(-2) + ('0' + date.getMinutes()).slice(-2) + ('0' + date.getSeconds()).slice(-2);
            }
            // 
            var path = MovizoPlayer._resolvePath(this.options, this.fileName);
            return url.replace('{MOVIZO_PATH}', path);
        }
        else {
            return MovizoPlayer._resolveURL(this.options, this.fileName);
        }
    }

    resolvePosterURL() {
        return MovizoPlayer.posterURL(this.options);
    }

    //////////////////////////////////////////////////////
    // HTMLMediaElement
    // https://developer.mozilla.org/en-US/docs/Web/API/HTMLMediaElement
    //////////////////////////////////////////////////////

        addEventListener(eventName, listener) {
            if (this.videoElement != null) {
                this.videoElement.addEventListener(eventName, listener);
            }
            else if (this.objectElement != null) {
                this.objectElement.addEventListener(eventName, listener);
            }
        }

    play() {
        if (!this.ready) {
            // 
            return;
        }
        if (this.videoElement != null) {
            this.videoElement.play();
        }
        else if (this.objectElement != null) {
            if (this.objectElement.play)  // gibiPlayer.swf
                this.objectElement.play();
        }
    }

    pause() {
        if (!this.ready) {
            // 
            return;
        }
        if (this.videoElement != null) {
            this.videoElement.pause();
        }
        else if (this.objectElement != null) {
            if (this.objectElement.pause)  // gibiPlayer.swf
                this.objectElement.pause();
        }
    }

    rewindPlay() {
        if (this.objectElement != null && this.objectElement.reloadMedia) {
            // gibiPlayer.swf
            this.objectElement.reloadMedia();
        }
        else {
            this.currentTime = 0;
        }
        this.play();
    }

    // @return seconds
    get currentTime() {
        if (this.videoElement != null) {
            return this.videoElement.currentTime;
        }
        else if (this.objectElement != null) {
            if (this.objectElement.getCurrentTime)  // gibiPlayer.swf
                return this.objectElement.getCurrentTime();
        }
        return null;
    }

    // @return seconds
    set currentTime(value) {
        if (this.videoElement != null) {
            this.videoElement.currentTime = value;
        }
        else if (this.objectElement != null) {
            if (this.objectElement.seek)  // gibiPlayer.swf
                this.objectElement.seek(value);
        }
    }

    // @return seconds
    get duration() {
        if (this.videoElement != null) {
            return this.videoElement.duration;
        }
        else if (this.objectElement != null) {
            if (this.objectElement.getDuration)  // gibiPlayer.swf
                return this.objectElement.getDuration();
        }
        return null;
    }

    get paused() {
        if (this.videoElement != null) {
            return this.videoElement.paused;
        }
        else if (this.objectElement != null) {
            // attach  InlinePlayerControls#activate  gibiPlayer.swf 
            // setupExternalInterface 
            if (this.objectElement.getPaused)
                return this.objectElement.getPaused();
        }
        return true;
    }

    get ended() {
        if (this.videoElement != null) {
            return this.videoElement.ended;
        }
        else if (this.objectElement != null) {
            if (this.objectElement.getEnded)  // gibiPlayer.swf
                return this.objectElement.getEnded();
        }
        return false;
    }

    get muted() {
        if (this.videoElement != null) {
            return this.videoElement.muted;
        }
        else if (this.objectElement != null) {
            // attach  InlinePlayerControls#activate  gibiPlayer.swf 
            // setupExternalInterface 
            if (this.objectElement.getMuted)
                return this.objectElement.getMuted();
        }
        return this.options.muted;
    }

    set muted(value) {
        if (this.videoElement != null) {
            this.videoElement.muted = value;
        }
        else if (this.objectElement != null) {
            if (this.objectElement.setMuted)  // gibiPlayer.swf
                this.objectElement.setMuted(value);
        }
        this.options.muted = value;
    }

    // 0.0 .. 1.0
    get volume() {
        if (this.videoElement != null) {
            return this.videoElement.volume;
        }
        else if (this.objectElement != null) {
            // attach  InlinePlayerControls#activate  gibiPlayer.swf 
            // setupExternalInterface 
            if (this.objectElement.getVolume) {
                return this.objectElement.getVolume();
            }
        }
        return 1.0;
    }

    set volume(value) {
        if (this.videoElement != null) {
            this.videoElement.volume = value;
        }
        else if (this.objectElement != null) {
            if (this.objectElement.setVolume) {
                this.objectElement.setVolume(value);
            }
        }
    }

    get loop() {
        if (this.videoElement != null) {
            return this.videoElement.loop;
        }
        else if (this.objectElement != null) {
            if (this.objectElement.getLoop)  // gibiPlayer.swf
                return this.objectElement.getLoop();
        }
        return this.options.loop;
    }

    set loop(value) {
        if (this.videoElement != null) {
            this.videoElement.loop = value;
        }
        else if (this.objectElement != null) {
            if (this.objectElement.setLoop)  // gibiPlayer.swf
                this.objectElement.setLoop(value);
        }
        this.options.loop = value;
    }

    // videoElementInlinePlayerControls InlinePlayer
    get hasAudio() {
        if (this.videoElement != null) {
            //var audio = this.videoElement.audioTracks;
            //return audio && audio.length > 0;
            // audioTracks  Firefox 
            //  video audio
            return true;
        }
        return true;
    }

}

